const MentoringSessions = () => {
  const [isOpenModal, openModal, closeModal] = useBooleanState();
  const [selectedSession, selectSession] = useState<MentoringSession | null>(null);

  return (
    <Box
      sx={{
        marginTop: {
          md: '52px',
          sm: '24px',
        },
      }}
    >
      <Typography sx={{ fontWeight: 'bold', marginBottom: '16px', fontSize: 18 }}>Mentoring Sessions</Typography>
      <Box sx={{ marginLeft: '-12px' }} display="flex" flexWrap="wrap">
        {sessions.map(({ title, color, description }) => (
          <Box
            component={FadeUp}
            key={title}
            sx={{
              flexGrow: 1,
              marginBottom: 1.5,
              marginLeft: 1,
              cursor: 'pointer',
              transition: 'transform .2s ease-in',
              boxShadow: 1,
              '&:hover': {
                transform: 'translateY(-6px)',
              },
            }}
            onClick={() => {
              selectSession({ title, color, description });
              openModal();
            }}
          >
            <Typography
              sx={{
                color: '#ffffff',
                padding: {
                  md: '12px 16px',
                  sm: '10px 8px',
                },
                borderRadius: '8px',
                whiteSpace: 'nowrap',
                textAlign: 'center',
                fontWeight: 'bold',
                fontSize: {
                  md: 16,
                  sm: 14,
                  sx: 12,
                },
                backgroundColor: color,
              }}
            >
              {title}
            </Typography>
          </Box>
        ))}
      </Box>
      <SessionDialog
        open={isOpenModal}
        onClose={() => {
          closeModal();
          selectSession(null);
        }}
        session={selectedSession}
      />
    </Box>
  );
};

interface Props extends ComponentProps<typeof Dialog> {
  session: MentoringSession | null;
}
const SessionDialog = ({ session, ...rest }: Props) => {
  return (
    <Dialog {...rest} sx={{ padding: 5 }}>
      <DialogTitle sx={{ textAlign: 'center', '& h2': { fontWeight: 'bold' } }}>{session?.title}</DialogTitle>
      <DialogContent>
        <DialogContentText
          sx={{ whiteSpace: 'pre-line', color: 'common.black', marginBottom: 5 }}
          dangerouslySetInnerHTML={{ __html: session?.description ?? '' }}
        />
      </DialogContent>
    </Dialog>
  );
};

export default MentoringSessions;
